/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'ArcadeClassic';
  src: url("/fonts/ArcadeClassic.eot");
  src: url("/fonts/ArcadeClassic.eot?#iefix") format("embedded-opentype"), url("/fonts/ArcadeClassic.woff2") format("woff2"), url("/fonts/ArcadeClassic.woff") format("woff"), url("/fonts/ArcadeClassic.ttf") format("truetype"), url("/fonts/ArcadeClassic.svg#ArcadeClassic") format("svg");
  font-weight: normal;
  font-style: normal; }

header {
  background: linear-gradient(rgba(0, 0, 0, 0.5)), url(/images/mobile-background.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  padding: 20px; }
  header p {
    color: white;
    text-align: center;
    font-size: 26px;
    font-family: 'ArcadeClassic';
    font-weight: normal;
    font-style: normal;
    word-spacing: 30px;
    filter: blur(0.007em);
    text-shadow: 0 0 0.15em #1da9cc;
    animation: shake 5s linear forwards infinite; }

@keyframes shake {
  5%, 15%, 25%, 35%, 55%, 65%, 75%, 95% {
    filter: blur(0.018em);
    transform: translateY(0.05em) rotate(0deg); }
  10%, 30%, 40%, 50%, 70%, 80%, 90% {
    filter: blur(0.01em);
    transform: translateY(-0.05em) rotate(0deg); }
  20%, 60% {
    filter: blur(0.03em);
    transform: translate(-0.05em, 0.05em) rotate(0deg); }
  45%, 85% {
    filter: blur(0.03em);
    transform: translate(0.05em, -0.05em) rotate(0deg); }
  100% {
    filter: blur(0.007em);
    transform: translate(0) rotate(-0.5deg); } }

body {
  background-color: #204056; }

.exp {
  display: flex;
  flex-direction: column; }
  .exp h2 {
    color: #c1c730;
    font-family: 'ArcadeClassic';
    font-weight: normal;
    font-style: normal;
    font-size: 20px;
    text-align: center;
    word-spacing: 5px;
    padding: 15px 0; }
  .exp .video {
    align-self: center;
    width: 95%;
    padding-bottom: 10px; }
    .exp .video iframe {
      height: 200px;
      width: 100%; }

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px; }

.score-board {
  display: flex;
  width: 50%;
  font-family: 'ArcadeClassic';
  font-weight: normal;
  font-style: normal;
  font-size: 30px;
  color: white;
  border: 2px dotted #c1c730;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  position: relative; }
  .score-board .badge {
    color: white;
    background-color: #204056;
    border: 2px solid lightcoral;
    font-size: 20px;
    padding: 2px 10px; }
  .score-board #user-label {
    position: absolute;
    top: 50;
    left: -35px;
    border-radius: 5px; }
  .score-board #computer-label {
    position: absolute;
    top: 50;
    right: -25px;
    border-radius: 5px; }

html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img {
  width: 50px;
  height: 50px; }
